import React, { createContext, useReducer } from "react";
import { createGlobalStyle } from "styled-components";
import Content from "./modules/common/Content";
import Header from "./modules/common/Header";

export const OrderContext = createContext();

const GlobalStyle = createGlobalStyle`
  body {
    width: 100%;
    margin: 0 auto;
  }
`;

function addOrderItem(state, newItem) {
  let productExists = false;
  let newList = [];
  newList = state.items.map((item) => {
    if (item.id === newItem.id) {
      productExists = true;
      item.qtd++;
      item.TotalAmount = item.TotalAmount + newItem.amount;
    }
    return item;
  });

  if (!productExists) {
    newItem.qtd = 1;
    newItem.TotalAmount = newItem.amount;
    newList.push(newItem);
  }
  return newList;
}

function removeOrderItem(state, newItem) {
  return state.items.map((item) => {
    if (item.id === newItem.id) {
      if (item.qtd > 0) {
        item.qtd--;
        item.TotalAmount = item.TotalAmount - newItem.amount;
      }
    }
    return item;
  });
}

function App() {
  const memoizedReducer = React.useCallback((state, action) => {
    switch (action.type) {
      case "add":
        return {
          ...state,
          items: addOrderItem(state, action.payload),
        };

      case "remove":
        return {
          ...state,
          items: removeOrderItem(state, action.payload),
        };

      case "clean":
        return {
          ...state,
          items: [],
        };
      default:
        return state;
    }
  }, []);

  const orderInitialValue = {
    items: [],
    getItemQtd: (order, id) => {
      const item = order.items.filter((item) => item.id === id)[0];
      return item ? item.qtd : 0;
    },
    getTotal: (order) => {
      let qtd = 0;
      let amount = 0;
      order.items.forEach((item) => {
        qtd = qtd + item.qtd;
        amount = amount + item.TotalAmount;
      });
      return { qtd, amount };
    },
  };

  const [order, setOrder] = useReducer(memoizedReducer, orderInitialValue);

  return (
    <div>
      <GlobalStyle />
      <OrderContext.Provider value={{ order, setOrder }}>
        <Header />
        <Content />
      </OrderContext.Provider>
    </div>
  );
}
export default App;
